.barra-menu{z-index:20;position:fixed;width:100%;background-color:#212531;box-shadow:0px 3px 12px rgba(0,0,0,0.575)}.barra-menu nav{display:flex;align-items:center;width:100%}.barra-menu .barra-menu-logo{flex:1 1;padding:1em;width:9em;margin-left:50px}.barra-menu .barra-menu-logo a{color:white;font-size:30px;text-decoration:none;transition:font-size 400ms ease-in}.barra-menu .barra-menu-nav{flex:1 1;margin:0;padding:0;display:flex;list-style:none;margin-right:20px}.barra-menu .barra-menu-nav-link-router:nth-child(odd){flex:1 1;text-decoration:none}.barra-menu .barra-menu-nav-link-router:nth-child(odd) li a{border:1.5px dashed #7a0f6c;text-decoration:none;color:white;padding:5px 20px 5px 20px;transition:500ms;border-radius:0 15px}.barra-menu .barra-menu-nav-link-router:nth-child(odd) li a:hover{background-color:#7a0f6c}.barra-menu .barra-menu-nav-link-router:nth-child(even){flex:1 1;text-decoration:none}.barra-menu .barra-menu-nav-link-router:nth-child(even) li a{border:1.5px dashed #247db8;text-decoration:none;color:white;padding:5px 20px 5px 20px;transition:500ms;border-radius:15px 0}.barra-menu .barra-menu-nav-link-router:nth-child(even) li a:hover{background-color:#247db8}.barra-menu.scrolled{-webkit-animation-name:scrolled-animation;animation-name:scrolled-animation;-webkit-animation-duration:500ms;animation-duration:500ms;background-color:rgba(39,37,49,0.973);font-size:13px !important;height:auto}.barra-menu.scrolled .barra-menu-logo a{color:#f8f8f8;font-size:1.6em;text-decoration:none}.barra-menu.scrolled .boton-hamburguesa-linea{border-radius:10px;height:2px;width:2.6em;background:#fff}@-webkit-keyframes scrolled-animation{0%{opacity:0%;top:-10px}100%{left:0px;top:0}}@keyframes scrolled-animation{0%{opacity:0%;top:-10px}100%{left:0px;top:0}}@media (max-width: 1075px){.barra-menu-nav{display:none !important}.barra-menu-logo{margin-left:55% !important}}@media (max-width: 500px){.barra-menu-logo{margin-left:45% !important}}@media (min-width: 1075px){.boton-hamburguesa{display:none !important}}.icono-compras{padding:0px 12px 0px 9px}.logoTipo{width:4em}

.boton-hamburguesa{background:transparent;margin-left:20px;height:2.2em;width:2.8em;border:none;cursor:pointer;padding:0;box-sizing:border-box;display:flex;flex-direction:column;justify-content:space-around}.boton-hamburguesa:focus{outline:none}.boton-hamburguesa-linea{border-radius:10px;height:3px;width:2.8em;background:#ffc400}

*{margin:0;padding:0}.nav-lateral{padding:0;position:fixed;height:100vh;background:linear-gradient(40deg, #151827, #24555e);width:70%;max-width:500px;z-index:300;margin:0;padding:0;transform:translateX(-100%);transition:transform 500ms ease-out}.nav-lateral ul{height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;list-style:none;padding:0}.nav-lateral ul li:nth-child(odd){padding:20px 0 20px 0}.nav-lateral ul li:nth-child(odd) a{background:#f0eeef;border-radius:0 10px;padding:5px 35px 5px 35px;font-size:1.4em;color:#16182e;text-decoration:none}.nav-lateral ul li:nth-child(even){padding:20px 0 20px 0;text-align:center;width:400px}.nav-lateral ul li:nth-child(even) a{background:#304868;border-radius:10px 0;padding:5px 35px 5px 35px;font-size:1.4em;color:white;text-decoration:none}.nav-lateral ul li:nth-child(1) a{padding:5px 78px 5px 78px}.nav-lateral ul li a:hover{background:#b18139}.nav-lateral .botonCerrar{margin:0;padding:0;text-align:center;display:flex;margin-left:88%;margin-right:auto;justify-content:flex-end;position:absolute;background:transparent;border:none;color:rgba(255,255,255,0.658);font-family:'Audiowide', cursive;font-size:1.6em;margin-top:13px;outline:none;cursor:pointer}.nav-lateral .botonCerrar:hover{color:#fff}.nav-lateral.open{transform:translateX(0)}

.fondo-negro{position:fixed;width:100%;height:100%;color:#9e4f4f;background-color:rgba(0,0,0,0.548);z-index:200}

.inicio-header{z-index:10;position:relative;top:90px;background:linear-gradient(0deg, #0e97b9, #17618b);border-bottom:0.5em solid #d6edff;margin-bottom:70px}.inicio-header-fondo{display:none;position:relative;background:linear-gradient(0deg, #0e97b9, #17618b);width:100%;height:40vh;border-bottom:0.5em solid #d6edff}.inicio-header-contenido{display:flex;flex-wrap:wrap;justify-content:space-around}.inicio-header-contenido-mujerMascarilla{padding:1.5em 0.5em 0 0.5em;height:25em}.inicio-header-contenido-descripcion{margin-top:2.3em;padding:1.5em 2.5em 1.5em 2.5em;color:white;background-color:#053850;border-top:5px solid #ffc965;height:18em;max-width:700px;border-radius:0 0 40px 40px;line-height:2.2em}.inicio-header-contenido-descripcion h1{text-align:center;margin-top:20px}.inicio-header-contenido-descripcion p{margin-top:6px}@media (max-width: 1183px){.inicio-header-contenido-descripcion{margin-top:0em;padding:1.5em 2.5em 1.5em 2.5em;color:white;border-top:0;border-left:7px solid #ffc965;height:20em;max-width:80%;border-radius:0 0px 0px 50px}}@media (max-width: 793px){.inicio-header-contenido-descripcion{margin-top:0em;padding:1.5em 2.5em 1.5em 2.5em;color:white;border-top:0;border-left:7px solid #ffc965;height:25em;max-width:80%;border-radius:0 0px 0px 50px}}@media (max-width: 583px){.inicio-header-contenido-descripcion{margin-top:0em;padding:1.5em 2.5em 1.5em 2.5em;color:white;border-top:0;border-left:7px solid #ffc965;height:30em;max-width:80%;border-radius:0 0px 0px 50px}}@media (max-width: 1183px){.inicio-header{z-index:10;position:relative;top:70px;background:transparent;padding-bottom:1em;border-bottom:0.5em solid rgba(62,89,139,0.349)}}

.icon-descripcion-content{margin-top:80px;background-color:#fff;padding:2em;display:flex;justify-content:space-around;flex-wrap:wrap}.icon-descripcion-content-serviceBox{padding:2em 2.5em 2.5em 2.5em;width:20em;background-color:#eff9ff;margin-bottom:1.4em;text-align:justify;box-shadow:4px 4px 10px rgba(184,202,221,0.815);transition:400ms ease-out}.icon-descripcion-content-serviceBox h1{padding:0.5em 0em 0.2em 0em;text-align:center;font-size:1.5em;color:#174870;border-bottom:1px solid #92badb}.icon-descripcion-content-serviceBox p{margin-top:15px;color:#353342;font-size:1.2em}.icon-descripcion-content-serviceBox:hover{background-color:#1b325c}.icon-descripcion-content-serviceBox:hover h1,.icon-descripcion-content-serviceBox:hover p,.icon-descripcion-content-serviceBox:hover i{color:white}.icon-descripcion-content-serviceBox-i{text-align:center;color:#17223a;font-size:2.5em}

.footer-info{border-top:1px solid #30384e;padding-top:40px;margin-left:5%;margin-right:5%;line-height:1.7em}.footer-info .footer-info-fila1{display:flex;justify-content:space-around;flex-wrap:wrap;padding:5px 0px 15px 0px}.footer-info .footer-info-fila1 a{text-decoration:none;color:#3f4a57}.footer-info .footer-info-fila1 a:hover{color:orange}.footer-info .footer-info-fila1 li{color:#3f4a57}.footer-info ul{list-style:none}.footer-info h3{color:#3f4a57}.footer-info .footer-info-fila2{text-align:center;color:#2f3e55;margin-bottom:2em}.footer-info .footer-info-fila2 a{text-decoration:none;color:#484b57}.footer-info .footer-info-fila2 a:hover{color:#4cc6db}.bloque{margin-bottom:15px;width:170px}

.header-productos-content-banner{padding-top:127px;padding-bottom:28px;background:linear-gradient(40deg, #bfcf2c, #abc710);width:100%}.header-productos-content-banner h1{color:white;text-shadow:3px 2px 5px rgba(50,64,82,0.466);border-left:5px solid #fff9bf;padding-left:0.8em;margin-left:1.2em;margin-right:2em;border-radius:5px 0px 0px 5px}

.pago-content{width:14em;margin-left:6px;margin-right:6px;margin-top:6px;margin-bottom:6px;background-color:#fff;padding:1.5em;border:1px solid #9ee1e4;text-align:center}.pago-content-box{padding:1em;width:12em;background-color:#e8f8ff}.pago-content-box:nth-child(odd){background-color:rgba(235,255,212,0.808)}.pago-content h1{color:#6ea7b1;text-align:center;font-size:1.2em;padding-bottom:16px}.pago-content-botton{margin-top:1.3em;padding:15px;font-size:1.1em;background:transparent;border:2px solid #17b917;color:#17b917;cursor:pointer;transition:400ms ease-in}.pago-content-botton:hover{background-color:#17b917;color:white}.pago-content .ic{font-size:1.8em;color:#184b55;margin-bottom:8px}

*{margin:0;padding:0}.item-box{width:330px;box-shadow:3px 3px 5px rgba(189,189,189,0.747);margin:6px;margin-bottom:2em;border:1px solid rgba(70,70,70,0.219);height:392px}.item-box-precio{display:flex;justify-content:flex-end;width:330px;position:absolute;margin-left:auto;margin-right:auto}.item-box-precio h3{background-color:#52a310;padding:6px;color:white}.item-box-img img{width:330px;height:170px}.item-box-titulo{text-align:center;margin-top:-5px;background-color:#f8f3e7;color:#382f06;padding:7px;font-size:1.2em}.item-box-descripcion{text-align:justify;line-height:1.7em;padding:15px 20px 15px 20px}.item-box button{background-color:#ffd902;padding:0.8em;font-size:1em;color:#262902;border-style:none;width:100%;cursor:pointer;transition:200ms ease-in}.item-box button:hover{background-color:#2c3331;color:white}.item-box-content{display:flex;flex-direction:column;height:185px;justify-content:space-between}

.productos-content{margin-top:40px;display:flex;margin-right:3%;justify-content:space-around;flex-wrap:wrap}.productos-content-items{justify-content:space-around;display:flex;flex-wrap:wrap;width:50em;margin-bottom:45px}.productos-content-filtro{margin-left:15px}.productos-content-pago{margin-bottom:2em}

.filter-container{padding:2em;border:1px solid #9ee1e4;width:250px;margin:10px;line-height:1.8em;margin-bottom:3em;color:#525c6b}.filter-container h2{color:#6ea7b1;padding:10px;border-bottom:1px solid #70b6c2}.filter-container p{margin-top:6px;margin-bottom:3px}.filter-container form{margin-left:18px}

.contacto-container{display:flex;justify-content:center;background-color:white;padding-top:115px;flex-wrap:wrap-reverse}.contacto-container-cajaContainer{width:289px;padding:1em}.contacto-container-cajaContainer h1{font-size:1.9em;padding-bottom:8px;border-bottom:1px solid #f8d820;color:#46445a}.contacto-container-cajaContainer-box{margin-top:1em;border:1px solid #a8ecf0;margin-bottom:15px;border-radius:15px 0}.contacto-container-cajaContainer-box a{text-decoration:none;transition:300ms ease-in}.contacto-container-cajaContainer-box a:hover{font-size:1.2em}.contacto-container-cajaContainer-box i{background-color:#36a536;padding:17px;color:white;border-radius:15px 0px 0px 0px}.contacto-container-cajaContainer-box i.facebook{background-color:#2b147c}.contacto-container-cajaContainer-box i.instagram{background:linear-gradient(20deg, #e0167b, #0981d1)}.contacto-container-cajaContainer-box i.twitter{background-color:#7fb0d8}.contacto-container-cajaContainer-box i.mail{background-color:red}.contacto-container-cajaContainer-box label{padding:15px;width:90px;color:#5a6264;cursor:pointer}.local{line-height:2.2em}iframe{max-width:100%}

